/* Стилі для коментарів */
.comments {
    margin-top: 20px;
    width: 100%;
    max-width: 600px;
}

.comment {
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
}

.author {
    font-weight: bold;
}

.time {
    font-size: 0.9em;
    color: #666;
}

.text {
    margin-top: 5px;
}
